    p.mb-5(data-aos='fade-right') A continuación, se presenta un resumen con otras normas internacionales de derechos humanos frente a la eliminación de la violencia y discriminación hacia las personas que componen los sectores LGBTIQ+, y orientaciones sexuales e identidades de género no hegemónicas, las cuales puede consultar para ampliar la información al respecto:
    .bg-info.mn.pb-0
      ImagenInfografica.color-acento-botones
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/9.svg', alt='Texto que describa la imagen')
          .tarjeta.bg-white.shadow.p-3(x="42%" y="23%" numero="+")
            .h5.mb-2 Declaración de Montreal sobre los Derechos Humanos de LGBT
            p Es formulada en 2006 en Montreal, Canadá, durante la Conferencia Internacional sobre los Derechos Humanos LGBT, establece una serie de derechos universales y libertades específicos para las personas LGBTIQ+. Esta declaración es el resultado de demandas y exigencias de la comunidad LGBTIQ+ y busca garantizar su protección y reconocimiento en el ámbito de los derechos humanos a nivel internacional.
          .tarjeta.bg-white.shadow.p-3(x="40%" y="33%" numero="+")
            .h5.mb-2 Observación General No. 20 de la ONU
            p En 2009, se hace referencia a la no discriminación y a los derechos económicos, sociales y culturales.
          .tarjeta.bg-white.shadow.p-3(x="38%" y="43%" numero="+")
            .h5.mb-2 Principios de Yogyakarta
            p En 2007, se establecieron los Principios de Yogyakarta, que son directrices para aplicar las leyes internacionales de derechos humanos en temas de orientación sexual e identidad de género, enfocándose en la comunidad LGBTIQ+. Diez años después, en 2017, se adoptaron los "Principios de Yogyakarta plus 10" como complemento a los originales.
          .tarjeta.bg-white.shadow.p-3(x="33%" y="53%" numero="+")
            .h5.mb-2 Resolución OEA 2435 
            p En 2008, la Asamblea General de la OEA reafirmó su compromiso con los principios de la Declaración Universal de Derechos Humanos, la cual establece la libertad y los derechos de los seres humanos sin distinción de raza, color, sexo, idioma, religión, opinión política o de cualquier otra índole, origen nacional o social, posición económica, nacimiento o cualquier otra condición.
          .tarjeta.bg-white.shadow.p-3(x="32%" y="63.5%" numero="+")
            .h5.mb-2 Resolución OEA 2504 
            p En 2009, se condenaron los actos de violencia y las violaciones de derechos humanos relacionadas, perpetradas contra individuos debido a su orientación sexual e identidad de género. En esta Asamblea, se solicitó a los Estados asegurar la investigación y el juicio de los actos de violencia y las violaciones de derechos humanos cometidos en estos contextos.
          .tarjeta.bg-white.shadow.p-3(x="58%" y="23%" numero="+")
            .h5.mb-2 Resolución 17/19 del Consejo de Derechos Humanos de la ONU
            p En 2011, la primera Resolución de las Naciones Unidas se enfocó en leyes, prácticas discriminatorias y actos de violencia contra personas LGBTI. De igual manera, instó a los Estados a derogar las penalizaciones de las relaciones consensuadas entre adultos del mismo sexo y a reconocer legalmente el género con el que las personas trans se identifican.
          .tarjeta.bg-white.shadow.p-3(x="60%" y="33%" numero="+")
            .h5.mb-2 Declaración de la ONU sobre orientación sexual e identidad de género
            p Formulada en 2018, esta Declaración condena la violencia, el acoso, la discriminación, la exclusión, la estigmatización y el prejuicio basados en la orientación sexual y la identidad de género. Además, condena los asesinatos, ejecuciones, torturas, arrestos arbitrarios y la privación de derechos económicos, sociales y culturales por estos motivos.
          .tarjeta.bg-white.shadow.p-3(x="62%" y="43%" numero="+")
            .h5.mb-2 Observación General 18 de la ONU 
            p En 1989, se hace referencia al término 'discriminación' y a cómo se caracteriza en dichos contextos. 
          .tarjeta.bg-white.shadow.p-3(x="66%" y="53.5%" numero="+")
            .h5.mb-2 Resolución OEA 2653 
            p En 2011, se solicitó a los Estados adoptar medidas necesarias para prevenir, sancionar y erradicar la discriminación, investigar tales actos y asegurar que las víctimas discriminadas por su orientación sexual reciban protección judicial en condiciones de igualdad. Además, se pidió que los responsables enfrenten las condenas definidas por la justicia de cada Estado.
          .tarjeta.bg-white.shadow.p-3(x="69%" y="63.5%" numero="+")
            .h5.mb-2 Resolución OEA 2600 
            p En 2010, se condenó la impunidad de los actos de violencia y se instó a los Estados Miembros a establecer mecanismos para combatir la discriminación contra personas por su orientación sexual e identidad de género.
    .bg-tema-2.mn.mb-5
      p Sumado a esto, la #[span.color-primario Asamblea General de las Naciones Unidas y el Consejo de Derechos Humanos], preocupados por la continua impunidad sobre las violaciones de los derechos humanos de las personas que, por su orientación sexual o identidad de género, han sido víctimas, establecieron una serie de resoluciones que han sido aprobadas bajo mandato, amparando la inclusión y garantía del respeto por los derechos humanos, aprobando las siguientes:
      .tarjeta.bg-white.p-5
        .tarjeta.tarjeta--gris.p-5.mb-5
          LineaTiempoC.color-acento-contenido(text-small)
            .row(titulo="")
              .col-lg-9.mb-4.mb-lg-0
                h4(style="color:#0F5FFE ")  A/HRC/RES/17/19
                p Documentar los actos de discriminación y violencia contra personas por su orientación sexual e identidad de género en todo el mundo. Evaluar cómo la normativa internacional de derechos humanos puede ser adoptada para poner fin a las acciones violentas originadas por la orientación sexual y la identidad de género.
              .col-lg-3.col-6: img(src='@/assets/curso/10.svg', alt='')
            .row(titulo="")
              .col-lg-9.mb-4.mb-lg-0
                h4(style="color:#0F5FFE ") A/HRC/RES/27/32 
                p Se solicita al Alto Comisionado actualizar el informe (A/HRC/19/41) con el fin de compartir buenas prácticas y mecanismos para superar la violencia y discriminación contra personas por su orientación sexual e identidad de género, en conformidad con las normas y el derecho internacional.
              .col-lg-3.col-6: img(src='@/assets/curso/11.svg', alt='')
            .row(titulo="")
              .col-lg-9.mb-4.mb-lg-0
                h4(style="color:#0F5FFE ") A/HRC/RES/32/2  
                p Reafirmar que todos los seres humanos nacen libres e iguales en dignidad y derechos sin excepción alguna por raza, color, sexo, idioma, religión, opinión política o de cualquier otra índole, origen nacional o social, posición económica, nacimiento o cualquier otra condición.
              .col-lg-3.col-6: img(src='@/assets/curso/12.svg', alt='')
            .row(titulo="")
              .col-lg-9.mb-4.mb-lg-0
                h4(style="color:#0F5FFE ")  A/HRC/RES/41/18
                p Protección contra la violencia y discriminación por motivos de orientación sexual e identidad de género.
              .col-lg-3.col-6: img(src='@/assets/curso/13.svg', alt='')
    p.mb-4(data-aos='fade-right') Para integrar los conceptos recién adquiridos, lo invitamos a sumergirse en la siguiente actividad de aprendizaje: 
        
