    #t_1_2.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 1.2 Normativa nacional: atención a población LGBTIQ+
    p.mb-5(data-aos='fade-right') Tal como lo decía Julieta Lemaitre Ripoll, en su artículo en la Revista Internacional de Derechos Humanos-Sur (2009):
    .bg-tema-3.mn
      .row.justify-content-center.mb-5.align-items-center
        .col-lg-8(data-aos="fade-right").mb-lg-0.mb-3
          .tarjeta(style="background-color: #DEE4F0").tarjeta-cita.p-5
              .tarjeta-cita__comilla1: img(src='@/assets/curso/comilla1.svg', alt='')
              .tarjeta-cita__comilla2: img(src='@/assets/curso/comilla2.svg', alt='') 
              p.fst-italic.mt-3 “Es  muy probable que la razón principal por la cual el derecho es un marco de referencia tan importante para las organizaciones LGBT sea la jurisprudencia. La Constitución de 1991, y en especial la interpretación que le dio la Corte Constitucional, han sido centrales para movilizar liderazgos, brindar un vocabulario y un escenario para hacer exigencias. Si bien la Constitución misma no menciona los derechos de los homosexuales, la Corte, en una serie de sentencias liberales, extendió los derechos a la igualdad y a la dignidad humana para incluir la protección contra la discriminación por orientación sexual. Con estas decisiones le dio un nuevo marco de significado a la orientación sexual de manera que ésta ha pasado de ser una cuestión de cultura y de “estilo de vida” a ser un problema de derechos.
                br
                br
                |La jurisprudencia sobre orientación sexual fue iniciada a mediados de la década del noventa por las demandas de tutela de individuos que, en general sin relación entre ellos, enmarcaban su sufrimiento como violaciones de derechos. De esta época se destaca el abogado Germán Rincón Perfetti, que empezó a utilizar de manera más o menos sistemática la acción de tutela para proteger los derechos de individuos discriminados por ser homosexuales.”
        .col-lg-4.col-7(data-aos="fade-left"): img(src='@/assets/curso/15.png', alt='')
    .mn.mb-5(style="background-color: #3c1c74 ").py-0
      .row.justify-content-center.align-items-center
        .col-lg-5.col-7.mb-lg-0.mb-3: img(src='@/assets/curso/16.svg', alt='' style="margin-top: -2rem")
        .col-lg-7(data-aos="fade-right")
          p.mb-0.text-white.p-4 La Constitución Política de Colombia, promulgada en 1991, es un documento clave que define la estructura y los principios del Estado colombiano. No solo establece la organización política y administrativa del país, sino que también consagra los derechos y deberes de los ciudadanos y las instituciones. La Constitución tiene como objetivos fundamentales garantizar la convivencia pacífica, respetar la dignidad humana y reforzar un Estado social de derecho, comprometiéndose a fortalecer la unidad nacional y asegurar a sus integrantes derechos fundamentales como la vida, la justicia, la igualdad, y la libertad. Entre ellos están: 
    .row.justify-content-center.mb-5
      .col-lg-8.position-relative 
        .avatar
          figure
            img(src="@/assets/curso/f1.svg", alt="alt").img250
        .crd.crd--avatarHorizontal.crd--first.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/i1.svg", alt="alt").img100
            .col
              p Artículo 1. Colombia es un Estado social de derecho, (...) organizado en forma de República unitaria democrática, participativa y pluralista, fundada en el respeto de la dignidad humana. 
        .crd.crd--avatarHorizontal.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/i2.svg", alt="alt").img100
            .col
              p Artículo 2. Son fines esenciales del Estado:(...) garantizar la efectividad de los principios, derechos y deberes consagrados en la Constitución.
        .crd.crd--avatarHorizontal.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/i2.svg", alt="alt").img100
            .col
              p Artículo 5. El Estado reconoce, sin discriminación alguna, la primacía de los derechos inalienables de la persona...”
        .crd.crd--avatarHorizontal.crd--last.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/i1.svg", alt="alt").img100
            .col
              p Artículo 13. Todas las personas nacen libres e iguales ante la ley, recibirán la misma protección y trato de las autoridades y gozarán de los mismos derechos, libertades y oportunidades sin ninguna discriminación por razones de sexo, raza, origen nacional o familiar, lengua, religión, opinión política o filosófica”. “El Estado promoverá las condiciones para que la igualdad sea real y efectiva y adoptará medidas en favor de grupos discriminados o marginados”
        








