    p.mb-5(data-aos='fade-right') En este contexto histórico, se presenta el marco normativo colombiano que da cuenta a la exigibilidad de los derechos humanos y la garantía del ejercicio efectivo de los derechos de las personas que componen los sectores LGBTIQ+ y las orientaciones sexuales e identidades de género no hegemónicas:
    .tarjeta-slyder.tarjeta.p-5.mb-5
      SlyderE(indicadores="derecha")
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Ley Estatutaria 1751 de 2015
            p Declara la salud como un derecho humano fundamental, formulando la Política de Atención Integral en salud (PAIS) y al Modelo de Atención Integral Territorial (MAITE), anteriormente Modelo Integral de Atención en Salud (MIAS) con la configuración de la Ruta de Promoción y Mantenimiento de la Salud (Resolución 3280 de 2018).
          .col-lg-5
            figure
              img(src='@/assets/curso/18.png', alt='Texto que describa la imagen')
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Política Nacional de Sexualidad, Derechos Sexuales y Derechos Reproductivos (PNSDSDR)
            p Instauró el derecho al disfrute de la sexualidad asociado con el desarrollo de autonomía de lo(a)s personas.
          .col-lg-5
            figure
              img(src='@/assets/curso/19.png', alt='Texto que describa la imagen')
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Plan Decenal de Salud Pública 2022-2031
            p Incorpora las categorías de la diversidad que orientarán el abordaje de los sujetos de derecho y la cualificación de sus acciones.
          .col-lg-5
            figure
              img(src='@/assets/curso/20.png', alt='Texto que describa la imagen')
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Ley 1257 de 2008
            p Incorpora las categorías de la diversidad que orientarán el abordaje de los sujetos de derecho y la cualificación de sus acciones.
          .col-lg-5
            figure
              img(src='@/assets/curso/21.png', alt='Texto que describa la imagen')
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Ley 1438 de 2011
            p Por medio de la cual se reforma el Sistema General de Seguridad Social en Salud y se dictan otras disposiciones.
          .col-lg-5
            figure
              img(src='@/assets/curso/22.png', alt='Texto que describa la imagen')
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Ley antidiscriminación 1482 de 2011
            p Esta ley tiene por objeto garantizar la protección de los derechos de una persona, grupo de personas, comunidad o pueblo, que son vulnerados a través de actos de racismo o discriminación. Al igual define la “discriminación arbitraria como toda distinción, exclusión o restricción que carezca de justificación razonable, efectuada por agentes del Estado o particulares, y que cause privación, perturbación o amenaza en el ejercicio legítimo de los derechos fundamentales establecidos en la Constitución Política.
          .col-lg-5
            figure
              img(src='@/assets/curso/23.png', alt='Texto que describa la imagen')
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Ley 1616 de 2013
            p Esta Ley, precisa la atención integral en salud mental por talento humano, con recursos suficientes y pertinentes en salud para responder a las necesidades de salud mental de la población, incluyendo entre las acciones a desarrollar la promoción del buen trato y la prevención de las violencias como acciones transversales para mejora de la salud mental de la población.
          .col-lg-5
            figure
              img(src='@/assets/curso/24.png', alt='Texto que describa la imagen')
    p.mb-5(data-aos='fade-right') Otras normas que existen en Colombia son:
    .row.justify-content-center.mb-5
      .col-lg-5.col-7(data-aos="fade-right"): img(src='@/assets/curso/25.png', alt='') 
      .col-lg-7(data-aos="fade-left")
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta rounded-1 tarjeta--azul")
          .p-4(titulo="Decreto 762 de 2018")
            p Esta política pública tiene como objetivo garantizar el ejercicio pleno de derechos a las personas que componen los sectores LGBTIQ+. como parte de la producción, gestión social y bienestar colectivo de la ciudad.
          .p-4(titulo="Resolución 459 de 2012")
            p Por lo cual se adopta el Protocolo de Atención Integral en Salud para Víctimas de Violencia Sexual.
          .p-4(titulo="Resolución 0163 de 2013")
            p Establece Lineamientos Técnicos de Violencias Basadas en Género para las Comisarías de Familia.
          .p-4(titulo="Ley 1448 de 2011")
            p Artículo 13. Enfoque diferencial. reconoce que hay poblaciones con características particulares debido a su género, orientación sexual y situación de discapacidad. Por tal razón, se establecen las medidas de ayuda humanitaria, atención, asistencia y reparación integral.
          .p-4(titulo="Decreto 1066 de 2015")
            p Decreto Único Reglamentario del Sector Administrativo del Interior. En el marco de la revisión, se cita:
              br
              |Igualdad y no discriminación: donde se garantice el respeto y el derecho a la Igualdad; protección y trato a los familiares de las víctimas, sin distinción de etnia, identidad de género, orientación sexual, cultura, entre otras.
              br
              |Enfoque diferencial. Las autoridades adoptaran las medidas que reconozcan las particularidades poblacionales, principalmente de los sujetos de especial protección constitucional, es decir, aquellos que, por sus características de género, orientación sexual, se encuentren en condición de vulnerabilidad y que requieren una atención y protección diferenciada y la implementación de políticas de acción afirmativa, acordes con su situación.
          .p-4(titulo="Resolución 518 de 2015")
            p Resolución 518 de 2015 del Ministerio de Salud y Protección Social que aborda la Gestión de la Salud Pública y la Ejecución del Plan de Salud Pública de Intervenciones Colectivas – PIC.

          .p-4(titulo="Resolución 295 de 2023")
            p Resolución 295 de 2023 Ministerio de Salud y Protección Social: modifican la gestión de la salud pública, las responsabilidades de las entidades territoriales y de los ejecutores del Plan de Salud Pública de Intervenciones Colectivas, así como las condiciones para la ejecución.
    .mn.mb-5(style="background-color: #3c1c74 ").py-0
      .row.justify-content-center.align-items-center
