          p.mb-0.text-white.p-4 Acorde con las directrices internacionales, Colombia adopta los lineamientos establecidos en la Declaración Universal de los Derechos Humanos; por medio de leyes y políticas que garanticen el ejercicio efectivo de los derechos. En este sentido, y en el contexto de este curso, se iniciará con la Ley Estatutaria 1751 de 2015:       
        .col-lg-5.col-7: img(src='@/assets/curso/26.svg', alt='' style="margin-top: -2rem")
    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/Oi35klisYoU?si=g6WkpvjrPh7sPFrG" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Video: Leyenda del video
    .mn.mb-5(style="background-color: #3c1c74 ").py-0
      .row.justify-content-center.align-items-center
        .col-lg-5.col-7.mb-lg-0.mb-3: img(src='@/assets/curso/27.svg', alt='' style="margin-top: -2rem")
        .col-lg-7(data-aos="fade-right")
          p.mb-0.text-white.p-4 Por otro lado, se suma el dejar de asociar la sexualidad a las patologías que la afectan, posición que ha restringido logros sustanciales, para que la población colombiana, independientemente de su edad, sexo, identidad de género, orientación sexual, pertenencia étnica, cultura, situación económica, nivel educativo, la viva plenamente dentro de un marco de derechos: 
    p.mb-5(data-aos='fade-right') En el marco de la Política Nacional de Derechos Sexuales y Derechos Reproductivos –PNSDSDR, es necesario dar a conocer el concepto del goce de la vida sexual. Respecto a este concepto, el Ministerio de Salud manifiesta que:
    .bg-info2.mn.pb-0.mb-5
      ImagenInfografica.color-acento-botones
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/28.svg', alt='Texto que describa la imagen')
          .tarjeta.bg-white.shadow.p-3(x="38%" y="21.5%" numero="+")
            .h5.mb-2 Vinculación entre derechos sexuales con reproductivos
            p Se basa en los principios de autonomía, beneficencia, la no maleficencia y la justicia. <br> El límite para ejercer los derechos sexuales está influenciado por el momento evolutivo de niñas y niños, cuyo acceso deber ser gradual acorde con la madurez mental y física. 
          .tarjeta.bg-white.shadow.p-3(x="36%" y="31%" numero="+")
            .h5.mb-2 Integralidad y no fragmentación, garantía de derechos
            p Reconocer la integridad de las personas. <br> Los servicios de salud deben ser prestados de manera integral y completa con el objetivo de prevenir, curar o paliar una enfermedad.
          .tarjeta.bg-white.shadow.p-3(x="34%" y="40%" numero="+")
            .h5.mb-2 Diferenciación entre sexualidad y reproducción
            p Los derechos sexuales son independientes a los derechos reproductivos. <br> La sexualidad se maneja en el campo de la autonomía personal la cual es modelada en lo social, por esta razón se considera como una construcción social. <br> La reproducción se ha ligado a lo sexual, pero con el avance en el campo de la fertilidad, se considera que puede darse sin la mediación sexual.
          .tarjeta.bg-white.shadow.p-3(x="34%" y="50%" numero="+")
            .h5.mb-2 La sexualidad, condición humana
            p La sexualidad es prioritaria para el desarrollo humano, debe ser libre de carga moral y darse en el marco de los derechos y respeto por las personas. 
          .tarjeta.bg-white.shadow.p-3(x="35%" y="59.5%" numero="+")
            .h5.mb-2 Igualdad y equidad
            p Las personas son únicas, iguales y diversas. Búsqueda del trato diferencial a lo que es diferente.  <br> A las relaciones intergenéricas se les conoce como equidad de género. <br> Las relaciones económicas y sociales se les conoce como equidad social y política. <br> Para dar cumplimiento a la igualdad y equidad, las Políticas públicas buscan mejorar la salud de las personas de escasos recursos, grupos vulnerables y sujetos de especial protección.
          .tarjeta.bg-white.shadow.p-3(x="62%" y="21.5%" numero="+")
            .h5.mb-2 Reconocimiento de acciones para garantía de derechos
            p Las acciones para la garantía de los derechos se basan en el respeto, promoción, protección, exigibilidad y reivindicación.
          .tarjeta.bg-white.shadow.p-3(x="64%" y="31%" numero="+")
            .h5.mb-2 Responsabilidad con la sexualidad y reproducción
            p Ejercer de manera libre los derechos, con responsabilidad individual y social. <br> Se debe considerar la corresponsabilidad entre el Estado, la familia y la sociedad.
          .tarjeta.bg-white.shadow.p-3(x="66%" y="40%" numero="+")
            .h5.mb-2 Libertad sexual y libertad reproductiva
            p Autodeterminación sexual y reproductiva. <br> Se materializan por medio de las decisiones personales como expresión de la libertad individual y ciudadana. 
          .tarjeta.bg-white.shadow.p-3(x="66%" y="50%" numero="+")
            .h5.mb-2 Diversidad y discriminación
            p La diferencia de cada persona está ligada a la dignidad humana. <br> Cero discriminaciones por condición de género, orientación sexual, identidad de género, cultura y otros elementos. 
          .tarjeta.bg-white.shadow.p-3(x="65%" y="59.5%" numero="+")
            .h5.mb-2 Armonización
            p Sinergia entre los desarrollos conceptuales y normativos desarrollados en torno a los derechos sexuales y reproductivos. <br> La articulación con políticas nacionales e internacionales no deje generar contradicción y busca el logro de objetivos comunes.
    p.mb-5(data-aos='fade-right') En el marco de la Política Nacional de Derechos Sexuales y Derechos Reproductivos –PNSDSDR, es necesario dar a conocer el concepto del goce de la vida sexual. Respecto a este concepto, el Ministerio de Salud y Protección Social. manifiesta que:
    .mn.mb-5(style="background-color: #e1fbf3 ").py-0
      .row.justify-content-center.align-items-center
        .col-lg-5.col-7.mb-lg-0.mb-3: img(src='@/assets/curso/30.svg', alt='' style="margin-top: -2rem")
        .col-lg-7(data-aos="fade-right")
          .p-3
            h5 Derecho al goce, la satisfacción y la gratificación sexual
            p.mb-0 Este derecho parte del reconocimiento que tiene toda persona a sentir placer, a disfrutar de su sexualidad, a autosatisfacerse y experimentar con los sentidos, implica que todas las personas tienen el derecho a vivir su sexualidad sin vincularla a la reproducción.
    p.mb-5(data-aos='fade-right') La #[span.color-primario Política de Atención Integral en Salud] (PAIS) Resolución 429 de 2016,  centra el enfoque del sistema en la persona, a través de #[span.color-primario la regulación de la intervención de los responsables de garantizar la atención de la salud en condiciones de accesibilidad, aceptabilidad, oportunidad, continuidad e integralidad.] El enfoque diferencial es la estrategia que permite a la política de atención reconocer y organizarse frente a las diferencias de las personas y colectivos, como el caso de las personas que componen a los sectores LGBTI, identificado condiciones de vulnerabilidad asociadas a factores físicos, psíquicos, sociales, psicosociales, culturales (creencias y valores), económicos, ambientales, entre otros.
    .bg-info3.mn.pb-0.mb-5
      img(src='@/assets/curso/31.svg', alt='')
    p.mb-5(data-aos='fade-right') La #[span.color-primario Política pública para la garantía del ejercicio efectivo de los derechos] de las personas que componen los sectores LGBTIQ+ y con orientaciones sexuales e identidades de género no hegemónicas, que propende, #[span.color-primario Promover y garantizar el ejercicio de los derechos económicos, sociales y culturales, con énfasis en el derecho a la salud, educación, trabajo, vivienda, recreación, deporte y cultura.]
    .row.justify-content-center.mb-5.align-items-center
        .col-lg-8(data-aos="fade-right").mb-lg-0.mb-3
          .tarjeta(style="background-color: #2c3e50").tarjeta-cita.p-5
              .tarjeta-cita__comilla1: img(src='@/assets/curso/comilla1.svg', alt='')
              .tarjeta-cita__comilla2: img(src='@/assets/curso/comilla2.svg', alt='') 
              p.text-white.mt-3 Este objetivo buscará que se adopten medidas, de índole técnica y económica, destinadas a fortalecer progresivamente el acceso de las personas a los derechos económicos, sociales y culturales en condiciones de igualdad, y sin ningún tipo de limitación sospechosa en virtud de la orientación sexual o identidad de género diversa. Para el efecto, la política promoverá acciones positivas con el fin de facilitar, proporcionar y promover la plena efectividad de los derechos por medio de medidas administrativas que posibiliten el disfrute de estas garantías bajo un enfoque diferencial OS/IG. Lo anterior implica: (i) remover las barreras existentes en el acceso a los derechos, (ii) impedir que surjan nuevas barreras de acceso y goce de derechos, y (iii) adoptar medidas a favor de personas que componen los sectores LGBTIQ+., como sujetos de especial protección.” 
        .col-lg-4.col-7(data-aos="fade-left"): img(src='@/assets/curso/32.png', alt='')
    p.mb-5(data-aos='fade-right') En esta línea se construyó la de manera participativa el Plan Decenal de Salud Pública 2022-2031- PDSP para que el sistema de salud avance en la adaptación de los servicios de salud de acuerdo con las necesidades de la población de los sectores LGBTI, garantizando el acceso con igualdad de trato y oportunidades a las actividades de promoción, prevención, diagnóstico, tratamiento, rehabilitación y paliación.
    .mn.mb-5(style="background-color: #3c1c74 ").py-0
      .row.justify-content-center.align-items-center
        .col-lg-5.col-7.mb-lg-0.mb-3: img(src='@/assets/curso/32.svg', alt='' style="margin-top: -2rem")
        .col-lg-7(data-aos="fade-right")
          .p-3
            p.mb-0.text-white Es por eso, el personal médico ha requerido capacitación en materia de derechos humanos, derechos a #[span.color-acento-botones2 las personas que componen los sectores LGBTIQ+ y orientaciones sexuales e identidades de género no hegemónicas] y derechos sexuales y reproductivos, fomentado así reducir la discriminación y eliminar las #[span.color-acento-botones2 barreras] para la garantía del derecho a la salud para esta población.  
    .mn.mb-5.bg-tema-4.py-0
      p.mb-5(data-aos='fade-right') Así mismo, mencionaremos los demás planes: 
      p.mb-5(data-aos='fade-right') El #[span.color-primario Plan Territorial de Salud (PTS)], a través de sus objetivos permite:
      .row.justify-content-center.mb-5
        .col-lg-4.mb-lg-0.mb-3
          .tarjeta.overflow-hidden.bg-white.shadow.h-100
            img(src='@/assets/curso/33.svg', alt='')
            .p-4.text-center
              h5 Análisis de la situación en salud (ASIS):
              p Incluye a personas que se auto identifican como parte de los sectores LGBTIQ+ o con orientaciones sexuales e identidades de género no hegemónicas.
        .col-lg-4.mb-lg-0.mb-3
          .tarjeta.overflow-hidden.bg-white.shadow.h-100
            img(src='@/assets/curso/34.svg', alt='')
            .p-4.text-center
              h5 Planeación de actividades:
              p Se enfoca en las necesidades específicas de las personas, colectivos, organizaciones y otros que conforman los sectores LGBTI, impactando positivamente en sus territorios.
        .col-lg-4.mb-lg-0.mb-3
          .tarjeta.overflow-hidden.bg-white.shadow.h-100
            img(src='@/assets/curso/35.svg', alt='')
            .p-4.text-center
              h5 Comunicación, mercadeo y movilización social:
              p Se busca el cumplimiento de las actividades planeadas, incluyendo la atención con enfoque diferencial hacia los sectores LGBTIQ+.
      p.mb-5(data-aos='fade-right') #[span.color-primario El Plan de Intervenciones  Colectivas (PIC):] se centra en intervenciones, procedimientos y actividades para la promoción de la salud y la gestión del riesgo, dirigidas a grupos poblacionales durante todo el curso de la vida.


      .row.justify-content-center.mb-5
        .col-lg-8.position-relative 
          .avatar
            figure
              img(src="@/assets/curso/c4.svg", alt="alt").img250
          .crd.crd--avatarHorizontal.crd--first2.py-4
            .row.align-items-center
              .col-auto
                figure
                  img(src="@/assets/curso/c1.svg", alt="alt").img100
              .col
                p Busca generar procesos organizativos de base comunitaria con personas de los sectores LGBTIQ+, incluyendo orientaciones sexuales e identidades de género no hegemónicas. 
          .crd.crd--avatarHorizontal2.py-4
            .row.align-items-center
