              .col
                p Fortalece la reflexión crítica y la agencia de las personas de estos sectores, promoviendo su ciudadanía en salud y el reconocimiento de sus problemáticas específicas.
          .crd.crd--avatarHorizontal.crd--last2.py-4
            .row.align-items-center
              .col-auto
