              .col
                p Potencia la agencia de individuos y colectivos para la exigibilidad del derecho a la salud y la disminución de inequidades en poblaciones en condiciones de fragilidad social.


      p.mb-5(data-aos='fade-right') #[span.color-primario El Modelo Integral de Atención en Salud (MIAS):] establece las condiciones necesarias para garantizar una atención integral por parte de los agentes del Sistema de Salud. Esto se logra mediante la adopción de lineamientos técnicos y operativos en dos áreas clave: #[b la Ruta Integral de Atención para la Promoción y Mantenimiento de la Salud, y la Ruta Integral de Atención en Salud para la Población Materno Perinatal. Este último aspecto incluye específicamente la cobertura para personas transmasculinas.]
        br
        br
        |A continuación, se resalta los esfuerzos por la inclusión y el apoyo a la comunidad LGBTIQ+ en el ámbito de la salud:
    .bg-info3.mn.pb-0.mb-5
      img(src='@/assets/curso/36.svg', alt='')
    p.mb-5(data-aos='fade-right') Las anteriores normas, planes, modelos y demás, aunque han traído más beneficios reales que simbólicos, han presentado algunas limitaciones, es por esto que, como lo ha dicho Ripoll (2009): 
    .row.justify-content-center.mb-5.align-items-center
        .col-lg-8(data-aos="fade-right").mb-lg-0.mb-3
          .tarjeta(style="background-color: #2c3e50").tarjeta-cita.p-5
              .tarjeta-cita__comilla1: img(src='@/assets/curso/comilla1.svg', alt='')
              .tarjeta-cita__comilla2: img(src='@/assets/curso/comilla2.svg', alt='') 
              p.text-white.mt-3.fst-italic “Las sentencias de la Corte tienen un peso que supera la evaluación de costos y beneficios instrumentales, un peso que surge de su valor simbólico, de sus efectos sobre la autopercepción y la identidad social de las personas, un efecto que como argumentan García y Uprimny es “anticonformista” (GARCÍA; UPRIMNY, 2004, p. 493-495). Este efecto simbólico es un antídoto poderoso contra la percepción de sí mismo y de la vida social que ofrece la experiencia de la discriminación y, quizá, es también una especie de antídoto o conjuro contra las secuelas emocionales de la violencia — una “contra” que se finca en la posibilidad de resistir el poder interpretativo de la violencia usando para ello la fuerza simbólica del derecho.” 

              p(style='color: #A7F7DD ' ).fst-italic Ripoll (2009)
        .col-lg-4.col-7(data-aos="fade-left"): img(src='@/assets/curso/37.png', alt='')
    .mn.mb-5(style="background-color: #423755 ").py-0
      .row.justify-content-center.align-items-center
